/* Global Variables */
/* Mixins */
/* Global Styling */
* {
  margin: 0;
  padding: 0;
}

.logo {
  font-size: 4rem;
  color: #d3ab55;
  width: 110px;
  height: 110px;
  border: 3px solid #d3ab55;
  border-radius: 50%;
  margin-right: 1.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-name {
  font-family: "Nunito", sans-serif;
  font-size: 3.2rem;
  color: #bbb;
  font-weight: 300;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.sub-name {
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.2rem;
  color: #bbb;
  text-align: center;
  font-weight: 300;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.main-heading {
  font-family: "Dancing Script", cursive;
  font-size: 6rem;
  font-weight: 300;
  color: #d3ab55;
  margin-bottom: 2.5rem;
}

.sub-heading {
  font-family: "Josefin Sans", sans-serif;
  font-size: 3.8rem;
  font-weight: 300;
  color: #bbb;
  margin-bottom: 5rem;
}

.main-btn {
  width: 170px;
  height: 45px;
  font-family: "Josefin Sans", sans-serif;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #d3ab55;
  background-color: transparent;
  border: 1px solid #d3ab55;
  outline: none;
  cursor: pointer;
  transition: all 0.4s;
}
.main-btn:hover {
  background-color: #d3ab55;
  color: #262626;
}

.header {
  width: 100%;
  height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.6)), url(../images/bg.jpg) center no-repeat;
  background-size: cover;
}
.header .brand {
  display: flex;
  align-items: center;
  position: absolute;
  top: 15%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.header-banner {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}